import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './vendor-list.component.html',
  styleUrls: ['./vendor-list.component.css'],
})
export class VendorListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = '/box_management/vendor_add';
    this._component = 'Vendor List';
   this.initTables();
  }

  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
    'id',
    'actions',
    'company_name',
    'ntn_cnic',
    'name',
    'company_tel_number',
    'vendor_mobile',
    'vendor_email',
    'vendor_reference',
    'company_address',
    'vendor_bank',
    'account_title',
    'account_number',
    'branch_code'
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_VENDOR;
    if (this._ss.checkPermission(this.ACTION.STATUS)) {
      this._tbls[URLz.$].columns.splice(2, 0, 'activate')
    }
    this._formCreator(this._tbls[URLz.$]);
    this._refresh(URLz.$)
  }
}
