import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './stock-zimmidar.component.html',
  styleUrls: ['./stock-zimmidar.component.css','../receive-add/receive-add.component.css']
})
export class StockZimmidarComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    // this._pathLocation = '/donation/cell_master_add';
    this._component = 'Collector Stock ';
    // this.initForm();
    this.initTables();


  }
  initForm(){
    // this._fs._form = this._fb.group({
    //   organisation_id: ['', this._vs._val('Oranaization')],
    //   system_id: ['', this._vs._val('System')]
    // });
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'bg',
      'le',
      'ou',
      'su',
      'collector_id', // Collector | Employee Code == user_name
      'box_stock', // .reference_no
      'ou_stock', // .issued Zone Stock issued
      'sellBoxQty', // ??? Sell Box Qty
      'remaining' // ???
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_COL
    this._formCreator(this._tbls[URLz.$])
    this._tbls[URLz.$].formFilter = this._fb.group({
      organisation_id:['', this._vs._vals('Organisation')],
      system_id:['', this._vs._vals('System')]
    })
    this._tbls[URLz.$].formFilter.get('system_id').valueChanges.subscribe(val => {
      if(this.emptyCheck(val)){
        this._tbls[URLz.$].formFilter.get('system_id').patchValue(val, {emitEvent: false})
        this._refresh(URLz.$)
      }
    })
  // Subscribe System to View the List Later
    // this._refresh(URLz.$, '')
  }
  applyClass = { 'col-lg-6': true, 'col-md-6': true};
}
