import { Component, OnInit } from '@angular/core';
import { NavigationEnd } from '@angular/router';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './sell-list.component.html',
  styleUrls: ['./sell-list.component.css']
})
export class SellListComponent extends BaseListClass implements OnInit {
  status: number
  ngOnInit() {

    this.status = window?.location?.href?.indexOf('distribution') == -1 ? 1 : 2;
    this._component =  ((this.status == 2 ? 'Distribution' : 'Sell') +' Box');
    this._pathLocation = '/box_management/box_sell' + (this.status == 2 ? '_distribution': '') + '_add';
    this.routerStrategy()
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'date',
      'bg',
      'le',
      'ou',
      'su',
      'department_id',
      'name',
      'mobile',
      'followup',
      'address',
      'near_by',
      'reference_no',
      'box_qty',
      'price_per_box',
      'collector_id',
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_SELL
    this._formCreator(this._tbls[URLz.$])
    this._tbls[URLz.$].formFilter = this._fb.group({
      organisation_id:['', this._vs._vals('Organisation')],
      system_id:['', this._vs._vals('System')],
      status: [this.status]

    })
    this._tbls[URLz.$].formFilter.get('system_id').valueChanges.subscribe(val => {
      if(this.emptyCheck(val)){
        this._tbls[URLz.$].formFilter.get('system_id').patchValue(val, {emitEvent: false})
        this._refresh(URLz.$)
      }
    })
    // Subscribe System to View the List Later
    // this._refresh(URLz.$, '')
  }
  applyClass = { 'col-lg-6': true, 'col-md-6': true};
}
