import { Component, Injector, OnInit } from '@angular/core';
import { MatDialogConfig } from '@angular/material/dialog';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { ImgViewComponent } from 'src/app/shared/components/dialogs/img-view/img-view.component';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './receive-list.component.html',
  styleUrls: ['./receive-list.component.css']
})
export class ReceiveListComponent  extends BaseListClass implements OnInit {

  public viewerOptions: any = {
    navbar: false,
    transition: true,
    toolbar: {
      zoomIn: 4,
      zoomOut: 4,
      oneToOne: 4,
      reset: 4,
      // prev: 4,
      // play: {
      //   show: 4,
      //   size: 'large',
      // },
      // next: 4,
      rotateLeft: 4,
      rotateRight: 4,
      flipHorizontal: 4,
      flipVertical: 4,
    }
  };

  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = '/box_management/box_receive_add';
    this._component = 'Box Receive List';
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'organisation_id',
      'system_id',
      'receive_date',
      'vendor_id',
      'box_qty',
      'box_price',
      'ref_no',
      'box_img',
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_RECEIVE
    this._formCreator(this._tbls[URLz.$])
    this._refresh(URLz.$)
  }

  applyClass = { 'col-lg-6': true, 'col-md-6': true};

  openImage(data, title) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data, title}
    }
    const dialogRef = this._dialog.open(
      ImgViewComponent, config);
    dialogRef.afterClosed().subscribe();
  }


}
