import { HttpErrorResponse } from '@angular/common/http';
import { Component, Injector, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { Receive } from 'src/app/interface/box-management/receive';
import { ImgType } from 'src/app/interface/common/img-type';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { Custom } from 'src/app/static/custom';
import Swal from 'sweetalert2';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './receive-add.component.html',
  styleUrls: ['./receive-add.component.css'],
})
export class ReceiveAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.BOX_RECEIVE;
  }
  ngOnInit() {
    this._fhs._pathLocation = '/box_management/box_receive';
    this._component = 'Box Receiving'
    this.initForm();
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId != null && Number(this._activeId) > 0) this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group({
      organisation_id: ['', this._vs._vals('Oranaization')],
      system_id: ['', this._vs._vals('System')],
      receive_date: ['', this._vs._vals('Receive Date')],
      vendor_id: ['', this._vs._vals('Vendor')],
      box_qty: ['', this._vs._val('Box Qty', { num: 1, maxChar:10, minChar: 1, authorized:0 })],
      box_price: ['', this._vs._val('Box Price', { num: 1, maxChar:10, minChar: 1, authorized:0})],
      ref_no: ['', this._vs._val('Reference No', { num: 1, minChar:4, maxChar:20 })],
    });
    // bank: SelectOption;
  }
  patchData() {
    this._http.get({...this.param, resource: this._activeId }).subscribe((res: any) => {
      const data: Receive = res.data.row;
      this._fs._form.patchValue({
        ...data,
      });
      this.box_img.link = data.box_img;
    });
  }
  // Images Access
  box_img: ImgType = { display: 'Box Image' };
  _onSubmit() {
    return super._onSubmity({
      validate: () => {
        return (
          this._fs._form.invalid ||
          !this.box_img.link ||
          this.box_img.error
        )
      },
      body: (ps: PartialSubmit) => {
        const fd = new FormData();
        if (this.box_img.file) fd.append('box_img', this.box_img.file);
        if(this._activeId) {
          this._fs._form.addControl('id', new FormControl(this._activeId));
        }
        const data: Receive = this._fs._form.getRawValue()
        Custom.jsontoFormData(data, '', fd);
        ps.param.body = fd
      }
    })
  }
  applyClass = { 'col-lg-4': true, 'col-md-4': true};
}
