import { Component, OnInit } from '@angular/core';
import { MatDialogConfig } from '@angular/material/dialog';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { BoxDepositDialogComponent } from 'src/app/shared/components/dialogs/box-deposit-dialog/box-deposit-dialog.component';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './deposit-list.component.html',
  styleUrls: ['./deposit-list.component.css']
})
export class DepositListComponent extends BaseListClass implements OnInit {
  ngOnInit() {
    this._pathLocation = '/box_management/box_deposit_add';
    this._component = 'Box Deposit';
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'bg',
      'le',
      'ou',
      'su',
      'collector_id',
      'receipt_number',
      'amount',
      'bank_id',
      'branch_name',
      'deposit_slip',
      'expense_slip',
      'expense_amount',
      'total_collection_amount',
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_SELL_DEPOSIT
    this._formCreator(this._tbls[URLz.$])
    this._tbls[URLz.$].formFilter = this._fb.group({
      organisation_id:['', this._vs._vals('Organisation')],
      system_id:['', this._vs._vals('System')],
      date_from:['', this._vs._vals('From Date')],
      date_to:['', this._vs._vals('To Date')]
    })
    this._tbls[URLz.$].formFilter.get('system_id').valueChanges.subscribe(val => {
      if(this.emptyCheck(val)){
        this._tbls[URLz.$].formFilter.get('system_id').patchValue(val, {emitEvent: false})
        this._refresh(URLz.$)
      }
    })
  }

  openDialog(data) {
    const config: MatDialogConfig = {
      panelClass: "dialog-responsive",
      data:  {source: data}
    }
    const dialogRef = this._dialog.open(
      BoxDepositDialogComponent, config);
    dialogRef.afterClosed().subscribe(() => {return});
  }


  applyClass = { 'col-lg-6': true, 'col-md-6': true};
}


