import { Component, EventEmitter, Injector, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialogConfig } from '@angular/material/dialog';
import { NavigationEnd } from '@angular/router';
import { debounceTime } from 'rxjs';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { DonorBoxSell } from 'src/app/interface/box-management/donor-box-sell';
import { FullGSBDonorForm } from 'src/app/model/donor/full-donor-gsb.form';
import { BoxMgmtSellComponent } from 'src/app/shared/components/dialogs/box-mgmt-sell/box-mgmt-sell.component';
import { GSBComponent } from '../../donor/gsb/gsb.component';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './sell-add.component.html',
  styleUrls: ['./sell-add.component.css'],
  providers: [FullGSBDonorForm],
})
export class SellAddComponent extends BaseForm implements OnInit {
  status: number
  constructor(
    public injector: Injector,
    public fullGSBDonorForm: FullGSBDonorForm
  ) {
    super(injector);
    // Here is the issue
    // Child Component is Overriding the changes made by parent components
    this._fs._form = this.fullGSBDonorForm.initForm();
    this._fs._form.addControl(
      'box',
      this._fb.group({
        id: ['', this._vs._val('Id')],
        box_qty: ['', this._vs._val('Quantity')],
        box_stock_id: ['', this._vs._vals('Box Stock')],
      })
    );
    // Getting Id here is mendatory
    this._activeId = this._fhs._getURLParam('id');
    this.status = window?.location?.href?.indexOf('distribution') != -1 ? 2 : 1;
    this.routerStrategy()
  }
  ngOnInit(): void {
    this.param.endpoint = URLz.BOX_SELL;
    this._component = ((this.status == 2 ? 'Distribution' : 'Sell') + ' Box')
    this._fhs._pathLocation = '/box_management/box_sell' + (this.status == 2 ? '_distribution' : '');
    this.patchData();
  }
  enabledButton(): boolean {
    const group = this._fs._form.get('gsb') as FormGroup;
    return (
      group.get('bg').status == 'VALID' &&
      group.get('le').status == 'VALID' &&
      group.get('ou').status == 'VALID' &&
      group.get('su').status == 'VALID' &&
      group.get('collector_id').status == 'VALID'
    );
  }
  imgUrl: string
  showDialog() {
    const y: DonorBoxSell = this._fs._form.value;
    const gsb = y.gsb
    const query = {
      organisation_id: y.donor.organisation_id,
      bg: gsb.bg,
      le: gsb.le,
      ou: gsb.ou,
      su: gsb.su,
      collector_id: gsb.collector_id,
      status: this.status
    };
    this._http.gets({ query, endpoint: URLz.BOXES_COLLECTOR }).subscribe((res) => {
      const config: MatDialogConfig = {
        panelClass: 'dialog-responsive',
        data: { items: res.data.records },
      };
      const dialogRef = this._dialog.open(BoxMgmtSellComponent, config);
      dialogRef.afterClosed().subscribe((result) => {


        if (result != null) {
          this.imgUrl = result?.box_stock?.image;
          this._fs._form.get('box').patchValue({
            id: result?.id,
            box_stock_id: result?.box_stock_id
          })
          const control = this._fs._form.get('box.box_qty')
          control.clearValidators();
          control.addValidators([this._vs._val('Box Quantity', { min: 1, max: result?.issued })])
          control.updateValueAndValidity();
          this._fs._form.updateValueAndValidity()
        }
      });
    });
  }
  @ViewChild('GSBComponent') gsbComponent: GSBComponent;
  patchData() {
    if (this._activeId != null && Number(this._activeId) > 0) {
      this._http.get({ ...this.param, resource: this._activeId }).subscribe((res: any) => {
        const data = res.data.row;
        // this._fs._form.patchValue({
        //   donor: data.donor,
        //   additional_info: data.additional_info,
        //   gsb: data.gsb
        // });
        this.gsbComponent.fillData(data)
      });
    }
  }

}
