import { Component, Injector, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { BoxDeposit } from 'src/app/interface/box-management/box-deposit';
import { ImgType } from 'src/app/interface/common/img-type';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { ServerMultipleResponse } from 'src/app/interface/common/server-multiple-response';
import { HierarchyForm } from 'src/app/model/transaction/hierarchy.form';
import { Custom } from 'src/app/static/custom';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './deposit-add.component.html',
  styleUrls: ['./deposit-add.component.css'],
  providers: [HierarchyForm]
})
export class DepositAddComponent extends BaseListClass implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._fhs._pathLocation = 'box_management/box_sell';
    this._component = 'Box Deposit';
    this.param.endpoint = URLz.BOX_SELL_DEPOSIT;
    this.initForm();
    this.afterInitForm();
    this.initTables();
  }
  initForm() {
    const hierarchyForm = this.injector.get(HierarchyForm)
    this._fs._form = this._fb.group({
      ...hierarchyForm.initForm().controls,
      deposit_date: ['', this._vs._vals('Deposit Date')],
      bank_id: ['', this._vs._vals('Bank')],
      branch_name: ['', this._vs._val('Branch Name', { alphaNum: 1 })],
      receipt_number: ['', this._vs._val('Receipt No', { min: 1, max: 1000000000 })],
      amount: ['', this._vs._val('Amount', { min: 1, max: 1000000000, decimal: 1 })],
      is_expense_amount: [false],
      total_collection_amount: [''],
      date_from: ['', this._vs._vals('From Date')],
      date_to: ['', this._vs._vals('To Date')]
    });
    // expense_slip : string;
    // deposit_slip: string;
  }
  afterInitForm() {
    this.subscribeExpenseChanges();
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId) {
      this.patchData();
    } else {
      this.subscribeDateRangeChanges('date_from');
      this.subscribeDateRangeChanges('date_to');
    }
    this.serviceFormValueSubscritpion();
  }
  data: any
  patchData() {
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res) => {
        const data: BoxDeposit = this.data = res.data.row;
        this._fs._form.patchValue({
          organisation_id: data?.organisation_id,
          system_id: data?.system_id,
          bg: data?.bg,
          le: data?.le,
          ou: data?.ou,
          su: data?.su,
          deposit_date: data.deposit_date,
          bank_id: data.bank_id,
          branch_name: data.branch_name,
          receipt_number: data.receipt_number,
          amount: data.amount,
          expense_amount: data.expense_amount,
          total_collection_amount: data.total_collection_amount,
          date_from: data.date_from,
          date_to: data.date_to,
        });
        this.img_expense_slip.link = data.expense_slip;
        this.img_deposit_slip.link = data.deposit_slip;
      });
  }
  // Images Access
  img_expense_slip: ImgType = { display: 'Expense Slip' };
  img_deposit_slip: ImgType = { display: 'Deposit Slip' };
  _onSubmity() {
    const list: { id, disabled, deposit_id, deposit_status }[] = this._tbls[URLz.$].dataSource.data
    const finalList = []
    list.forEach(x => {
      if (x.deposit_id)finalList.push(x.id)
    })
    return super._onSubmity({
      validate: () => {
        const form = this._fs._form
        if (form.valid && this.img_deposit_slip.link &&
              !this.img_deposit_slip.error &&
              finalList.length > 0 &&
              this.addValidatorToAmount()){
            if (form.get('is_expense_amount').value){
              return (!this.img_expense_slip.link || this.img_expense_slip.error)
            }else {
              // Validatation Success
              return false
            }
          }else {
            return true
          }
      },
      body: (ps: PartialSubmit) => {
        const fd = new FormData();
        if (this.img_expense_slip.file) {
          fd.append('expense_slip', this.img_expense_slip.file)
        }
        if (this.img_deposit_slip.file) {
          fd.append('deposit_slip', this.img_deposit_slip.file)
        }
        if (this._activeId) {
          this._fs._form.addControl('id', new FormControl(this._activeId));
        }
        const result = this._fs._form.value
        result.sell_box_id = finalList
        Custom.jsontoFormData(result, '', fd);
        ps.param.body = fd
      }
    })
  }
  // Subscriptions
  addValidatorToAmount(){
    let totalSelection = 0;
    let selectedItems = 0
    this._tbls[URLz.$].dataSource.data.forEach(item => {
      if(item.deposit_id == true){
        selectedItems++
        const resultvalue = item?.box_stock?.price_per_box * item?.box_qty
        totalSelection += resultvalue
      }
    })
    const form = this._fs._form
    if(form.get('is_expense_amount').value && form.get('expense_amount')?.value){
      const expenseAmount = form.get('expense_amount').value
      totalSelection += +expenseAmount
    }
    const control = form.get('amount')
    control.clearValidators();
    if(totalSelection == 0){
      totalSelection = 1
    }
    control.addValidators([
      this._vs._val('Amount', {min: totalSelection, max:1000000000})
    ])
    const amount =  control.value
    if(amount < totalSelection){
      control.setErrors({
        key: "MIN",
        en:  'Minimum digits ' + totalSelection + ' allowed',
        ur: ' کم از کم'.concat(' ', totalSelection.toString(), ' ', 'ہندسے کی اجازت ہے۔')
      })
    }
    else if(amount > 0 && amount >= totalSelection){
      control.setErrors(null)
    }
    this._fs._form.updateValueAndValidity();
    return (amount > 0 && amount >= totalSelection && selectedItems)
  }
  subscribeExpenseChanges() {
    const subs = this._fs._form.get('is_expense_amount').valueChanges.subscribe(val => {
      if (val) {
        this._fs._form.addControl(
          'expense_amount',
          new FormControl(
            '', this._vs._val('Expense Amount', { num: 1, max: 1000000000 })
          ))

      } else {
        this._fs._form.removeControl('expense_amount')
      }
      this.addValidatorToAmount()
    })
    this.subscriptionArray.push(subs)
  }
  subscribeDateRangeChanges(controlName: string) {
    const subs = this._fs?._form?.get(controlName)?.valueChanges?.subscribe(val => {
      if (controlName == 'date_from') {
        this._tbls[URLz.$].dataSource.data = []
      } else if (val) {
        val = this._fhs._dateOnly(val)
        this.getDataByDate(val, (result: ServerMultipleResponse) => {
          this._tbls[URLz.$].dataSource.data = result.data.records
          this._refresh(URLz.$)
        });
      }
    })
    this.subscriptionArray.push(subs)
  }
  getDataByDate(
    // tslint:disable-next-line: variable-name
    date_to,
    handleNext : (result: ServerMultipleResponse) => void
  ) {
    const {
      organisation_id,
      system_id, bg, le, ou, su,
      date_from } = this._fs._form.value
    if (date_from && date_to && organisation_id && system_id && bg && le && ou && su) {
      this.listParam = {
        organisation_id,
        system_id,
        bg,
        le,
        ou,
        su,
        date_from,
        date_to,
      }
      this._http.gets({
        endpoint: URLz.BOX_SELL,
        query: {
          ...this.listParam
        }
      }).subscribe({
        next: (res) => {
          handleNext(res)
        }
      })
      // BOX_SELL
    } else {
      this._fs._form.markAllAsTouched();
    }
  }
  serviceFormValueSubscritpion() {
    // this._fs._form.valueChanges.subscribe(x => {

    // })
  }
  // List Handling
  initTables() {
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'date',
      'name',
      'mobile',
      'followup',
      'address',
      'near_by',
      'reference_no',
      'box_qty',
      'price_per_box',
      'total',
      'collector_id',
    ];
    this._tbls[URLz.$].endpoint = URLz.BOX_SELL
    this._formCreator(this._tbls[URLz.$])
    // this._refresh(URLz.$, '')
  }
}
