import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';

import { DepositAddComponent } from './deposit-add/deposit-add.component';
import { DepositListComponent } from './deposit-list/deposit-list.component';
import { ReceiveAddComponent } from './receive-add/receive-add.component';
import { ReceiveListComponent } from './receive-list/receive-list.component';
import { SellAddComponent } from './sell-add/sell-add.component';
import { SellListComponent } from './sell-list/sell-list.component';
import { StockZimmidarComponent } from './stock-zimmidar/stock-zimmidar.component';
import { StockZoneComponent } from './stock-zone/stock-zone.component';
import { StockComponent } from './stock/stock.component';
import { VendorAddComponent } from './vendor-add/vendor-add.component';
import { VendorListComponent } from './vendor-list/vendor-list.component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'vendor',
        component: VendorListComponent,
        data: {
          title: 'Vendor List',
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Vendor' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'vendor_add',
        component: VendorAddComponent,

        data: {
          title: 'Vendor Add',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Vendor' },
            { title: 'Add' },
          ],
        },
      },
      {
        path: 'box_receive',
        component: ReceiveListComponent,

        data: {
          title: 'Box Receive List',
          permission: [
            ACTION.VIEW,
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Receive' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_receive_add',
        component: ReceiveAddComponent,
        data: {
          title: 'Box Receive Add',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Receive' },
            { title: 'Add' },
          ],
        },
      },
      {
        path: 'box_stock',
        component: StockComponent,
        data: {
          title: 'Box Stock',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Stock' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_stock_ou',
        component: StockZoneComponent,
        data: {
          title: 'Box Stock OU',
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Stock OU' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_stock_collector',
        component: StockZimmidarComponent,
        data: {
          title: 'Box Collector',
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Stock Collector' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_sell_add',
        component: SellAddComponent,
        data: {
          title: 'Box Sell',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Sell' },
            { title: 'Add' },
          ],
        },
      },
      {
        path: 'box_sell_distribution_add',
        component: SellAddComponent,
        data: {
          title: 'Box Sell',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Sell' },
            { title: 'Add' },
          ],
        },
      },
      {
        path: 'box_sell',
        component: SellListComponent,
        data: {
          title: 'Box Sell',
          permission: [
            ACTION.VIEW,
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Sell' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_sell_distribution',
        component: SellListComponent,
        data: {
          title: 'Box Sell',
          permission: [
            ACTION.VIEW,
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Sell' },
            { title: 'List' },
          ],
        },
      },
      {
        path: 'box_deposit_add',
        component: DepositAddComponent,
        data: {
          title: 'Box Deposit',
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Deposit' },
            { title: 'Add' },
          ],
        },
      },
      {
        path: 'box_deposit',
        component: DepositListComponent,
        data: {
          title: 'Box Deposit',
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: 'Box Management' },
            { title: 'Box Deposit' },
            { title: 'List' },
          ],
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BoxManagementRoutingModule {}
